import { Component, h, Prop, getAssetPath, Host } from '@stencil/core';
import { isEmpty } from '@springbokagency/stencil-base';
import { IconName } from './icon.types';

/**
 * The icon component.
 */
@Component({
  tag: 'bs-icon',
  styleUrl: 'icon.scss',
  shadow: true,
  assetsDirs: ['assets'],
})
export class BsIcon {
  /**
   * Icon's name
   */
  @Prop() name: IconName;

  render() {
    if (!isEmpty(this.name)) {
      return (
        <Host>
          <ion-icon class="icon" aria-hidden="true" src={getAssetPath(`assets/${this.name}.svg`)} />
        </Host>
      );
    }

    return <Host></Host>;
  }
}
